body {
  overflow-x: hidden;
}

.site-header {
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.site-header .header-widget {
  display: flex;
}
.site-header .header-widget .wp-block-button {
  margin-bottom: 0;
}
.site-header .header-widget .wp-block-button .wp-block-button__link {
  padding: 6px 30px;
}
@media only screen and (max-width: 1080px) {
  .site-header .header-widget .wp-block-button .wp-block-button__link {
    padding: 8px 15px;
  }
}
@media only screen and (max-width: 1080px) {
  .site-header .header-widget {
    margin-left: 0;
  }
}
@media only screen and (min-width: 1180px) {
  .site-header .header-widget {
    justify-content: flex-end;
    width: 100%;
    max-width: 360px;
  }
}
@media only screen and (max-width: 860px) {
  .site-header .site-logo {
    order: 1;
  }
  .site-header #mobile-menu-control-wrapper {
    margin: 0;
    flex-basis: unset;
  }
  .site-header .main-navigation {
    order: 3;
  }
  .site-header .main-navigation .menu-toggle {
    padding: 0;
  }
  .site-header .header-widget {
    order: 2;
    flex-basis: unset;
    margin: 0 20px 0 auto;
  }
}
@media only screen and (max-width: 480px) {
  .site-header .site-logo .header-image {
    width: 105px;
  }
}

.site-footer .widget_nav_menu .menu-item a {
  font-size: 14px;
  line-height: 22px;
  text-decoration: none;
}
.site-footer .footer-bar {
  width: 100%;
  text-align: left;
}
.site-footer .footer-bar h2 {
  color: var(--white);
  font-size: 30px;
  line-height: 40px;
}
.site-footer .footer-bar .app-btns {
  display: flex;
  align-items: center;
}
.site-footer .footer-bar .app-btns a {
  margin: 0 15px 15px 0;
}
@media only screen and (max-width: 860px) {
  .site-footer .footer-widgets .footer-widgets-container {
    padding-right: 15px;
    padding-left: 15px;
  }
  .site-footer .footer-widgets .footer-widgets-container .inside-footer-widgets {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .site-footer .footer-widgets .footer-widgets-container .inside-footer-widgets > div {
    flex: unset;
    width: 100%;
    max-width: 50%;
  }
  .site-footer .footer-widgets .footer-widgets-container .inside-footer-widgets > div:nth-child(odd) {
    padding-right: 5px;
  }
  .site-footer .footer-widgets .footer-widgets-container .inside-footer-widgets > div:nth-child(even) {
    padding-left: 5px;
  }
  .site-footer .footer-widgets .footer-widgets-container .inside-footer-widgets > div:last-child {
    padding: 0;
    text-align: center;
  }
  .site-footer .footer-widgets .footer-widgets-container .widget-title {
    margin-bottom: 15px;
  }
}

input[type=email], input[type=number], input[type=password], input[type=search], input[type=tel], input[type=text], input[type=url], select {
  border-radius: 50px;
  width: 100%;
}

input[type=number] {
  appearance: textfield;
}

.dropdown-arrow {
  width: 10px;
  height: 10px;
  display: block;
  margin-left: 10px;
}

